import Vue from "vue";
import NetworkService from '../services/network.service';
import MessageService from '../services/message.service';
import auth from '../store/modules/auth';
import User from '../models';
export default Vue.extend({
    name: "Users",
    data: function () {
        return {
            user: auth.state.user,
            busy: false,
            usersData: [],
            tableFields: [
                {
                    prop: 'id',
                    label: 'Id',
                    minWidth: 60,
                    fixed: true,
                    sortable: true
                },
                {
                    prop: 'username',
                    label: 'Name',
                    minWidth: 90,
                    sortable: true
                },
                {
                    prop: 'roles',
                    label: 'Roles',
                    filters: [
                        { text: 'Visitor', value: 'visitor' },
                        { text: 'User', value: 'user' },
                        { text: 'Administrator', value: 'admin' }
                    ],
                    filterMethod: function (value, row) { return row.role === value; }
                }
            ],
            isAdmin: false
        };
    },
    created: function () {
        var _this = this;
        this.isAdmin = User.isAdmin(this.user);
        if (this.isAdmin) {
            this.busy = true;
            NetworkService.getUsers()
                .then(function (users) { return _this.usersData = users; })
                .catch(MessageService.showError)
                .then(function () { return _this.busy = false; });
        }
        else {
            this.usersData = [this.user];
        }
    },
    computed: {
        isMobile: function () {
            return window.innerWidth <= 768;
        }
    },
    methods: {
        detailClick: function (userId) {
            this.$router.push({ name: 'Profile', params: { id: userId } });
        },
        addNewUser: function () {
            this.$router.push({ name: 'Register' });
        },
        filterRole: function (value, row) {
            return row.role === value;
        }
    }
});



