import Vue from "vue";
import NetworkService from '../services/network.service';
import MessageService from '../services/message.service';
export default Vue.extend({
    name: "Register",
    data: function () {
        return {
            busy: false,
            registerForm: {
                username: '',
                password: '',
                roles: []
            },
            rules: {
                username: [
                    { required: true, message: 'Please input username', trigger: 'blur' }
                ],
                password: [
                    { required: true, message: 'Please enter password', trigger: 'blur' }
                ],
                roles: [
                    { required: true, message: 'Please select roles', trigger: 'blur' }
                ]
            },
            rolesList: []
        };
    },
    created: function () {
        var _this = this;
        this.busy = true;
        NetworkService.getRoles()
            .then(function (roles) { return _this.rolesList = roles; })
            .catch(MessageService.showError)
            .then(function () { return _this.busy = false; });
    },
    methods: {
        submitForm: function () {
            var _this = this;
            this.$refs['registerForm'].validate()
                .then(function () {
                _this.busy = true;
                var form = _this.registerForm;
                NetworkService
                    .register(form.username, form.password, form.roles)
                    .then(function () { return _this.$router.push({ name: 'Users' }); })
                    .catch(MessageService.showError)
                    .then(function () { return _this.busy = false; });
            })
                .catch(function () { return console.error('register form validation fail'); });
        }
    }
});



