import Vue from 'vue';
import Router from 'vue-router';
import auth from '../store/modules/auth';
import TokenService from '../services/token.service';
import { Main, Login, Profile, Settings, Accounts, AccountsIncome, AccountsCurrent, AccountsExpense, Transactions, Users, Register } from '../components/';
Vue.use(Router);
var main = {
    path: '/',
    name: 'Main',
    component: Main,
    meta: {
        localname: 'Main page'
    }
};
var login = {
    path: '/login',
    name: 'Login',
    component: Login,
    meta: {
        localname: 'Login page'
    }
};
var profile = {
    path: '/profile/:id?',
    name: 'Profile',
    component: Profile,
    meta: {
        localname: 'Profile page'
    },
    props: true
};
var settings = {
    path: '/settings',
    name: 'Settings',
    component: Settings,
    meta: {
        localname: 'Settings page'
    }
};
var accounts = {
    path: '/accounts',
    name: 'Accounts',
    component: Accounts,
    meta: {
        localname: 'Accounts page'
    }
};
var accountsIncome = {
    path: '/accounts-income',
    name: 'AccountsIncome',
    component: AccountsIncome,
    meta: {
        localname: 'Income accounts page'
    }
};
var accountsCurrent = {
    path: '/accounts-current',
    name: 'AccountsCurrent',
    component: AccountsCurrent,
    meta: {
        localname: 'Current accounts page'
    }
};
var accountsExpense = {
    path: '/accounts-expense',
    name: 'AccountsExpense',
    component: AccountsExpense,
    meta: {
        localname: 'Expense accounts page'
    }
};
var transactions = {
    path: '/transactions',
    name: 'Transactions',
    component: Transactions,
    meta: {
        localname: 'Transactions page'
    }
};
var users = {
    path: '/users',
    name: 'Users',
    component: Users,
    meta: {
        localname: 'Users page'
    }
};
var register = {
    path: '/register',
    name: 'Register',
    component: Register,
    meta: {
        requiresRoles: ['admin'],
        localname: 'Register page'
    }
};
var routes = [
    main,
    login,
    profile,
    settings,
    accounts,
    accountsIncome,
    accountsCurrent,
    accountsExpense,
    transactions,
    users,
    register
];
var router = new Router({
    routes: routes
});
router.beforeEach(function (to, from, next) {
    var isAuthorized = !!auth.state.user;
    if (!isAuthorized) {
        return to.name !== 'Login' ? next({ name: 'Login' }) : next();
    }
    if (isAuthorized) {
        return TokenService.checkLifetime()
            .then(function () {
            return to.name === 'Login' ? next({ name: 'Main' }) : checkRoles(to, from, next);
        })
            .catch(function (err) {
            auth.commitLogout();
            return next({ name: 'Login' });
        });
    }
});
function checkRoles(to, from, next) {
    if (!to.meta.requiresRoles)
        return next();
    var user = auth.state.user;
    var checkRole = function (role) { return to.meta.requiresRoles.includes(role); };
    user.roles.split(',').some(checkRole)
        ? next()
        : next({ name: 'Main' });
}
export default router;



