import Vue from "vue";
import NetworkService from '../services/network.service';
import MessageService from '../services/message.service';
export default Vue.extend({
    name: "Login",
    data: function () {
        return {
            loginForm: {
                username: '',
                password: ''
            },
            busy: false,
            rules: {
                username: [
                    { required: true, message: 'Please input username', trigger: 'blur' }
                ],
                password: [
                    { required: true, message: 'Please enter password', trigger: 'blur' }
                ]
            }
        };
    },
    methods: {
        submitForm: function () {
            var _this = this;
            this.$refs['loginForm'].validate()
                .then(function () {
                _this.busy = true;
                NetworkService
                    .login(_this.loginForm.username, _this.loginForm.password)
                    .then(function () { return _this.$router.push({ name: 'Main' }); })
                    .catch(MessageService.showError)
                    .then(function () { return _this.busy = false; });
            })
                .catch(function () { return console.error('login form validation fail'); });
        }
    }
});



