import Vue from 'vue';
import auth from '../../store/modules/auth';
import { TheHeader, TheAside, TheFooter } from '.';
export default Vue.extend({
    name: "TheLayout",
    components: { TheHeader: TheHeader, TheAside: TheAside, TheFooter: TheFooter },
