import Vue from 'vue';
import auth from '../store/modules/auth';
import NetworkService from '../services/network.service';
import MessageService from '../services/message.service';
import User from '../models';
export default Vue.extend({
    name: "Profile",
    data: function () {
        return {
            user: {},
            editMode: false,
            profileForm: {
                username: '',
                password: ''
            },
            busy: false,
            rules: {
                username: [
                    { required: true, message: 'Please input username', trigger: 'blur' }
                ],
                password: [
                    { min: 1, message: 'Please enter password', trigger: 'blur' }
                ]
            },
            isAdmin: false,
            isSelf: false
        };
    },
    props: ['id'],
    created: function () {
        this.isAdmin = User.isAdmin(auth.state.user);
        this.setupComponent();
    },
    methods: {
        setupComponent: function () {
            var _this = this;
            this.isSelf = this.id === auth.state.user.id;
            if (this.isAdmin && this.id && !this.isSelf) {
                NetworkService.getUserById(this.id)
                    .then(function (user) {
                    if (user) {
                        _this.user = user;
                        _this.profileForm.username = _this.user.username;
                    }
                    else {
                        _this.profileForm.username = auth.state.user.username;
                    }
                })
                    .catch(function (err) { return console.error(err.message); });
            }
            else {
                this.user = auth.state.user;
                this.profileForm.username = auth.state.user.username;
            }
        },
        editButtonPressed: function () {
            this.editMode = this.isAdmin;
        },
        deleteButtonPressed: function () {
            var _this = this;
            MessageService.showConfirmMessage('Delete confirmation', 'Are you sure to delete this user?')
                .then(function () {
                NetworkService.deleteUser(_this.id)
                    .then(function () { return _this.$router.push({ name: 'Users' }); })
                    .catch(MessageService.showError);
            })
                .catch();
        },
        submitForm: function () {
            console.log('submitForm');
        },
        cancelForm: function () {
            this.editMode = false;
        }
    },
    watch: {
        $route: function (to, from) {
            if (from.params.id !== to.params.id)
                this.setupComponent();
        }
    }
});



